import "./App.css";
import Section from "./Components/Section";
import "./Components/IconRow";
import IconRow from "./Components/IconRow";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGithub, faLinkedin } from "@fortawesome/free-brands-svg-icons";
import { faEnvelope, faPhone } from "@fortawesome/free-solid-svg-icons";
import Badge from "./Components/Badge";
import Element from "./Components/Element";
import technologies from "./Data/technologies";
import experience from "./Data/experience";
import projects from "./Data/projects";
import education from "./Data/education";
import skills from "./Data/skills";

function App() {
  const getBadges = (arr) => {
    return arr.map((element) => {
      return <Badge>{element}</Badge>;
    });
  };

  const getElements = (arr) => {
    return arr.map((element) => {
      return (
        <Element
          company={element.company}
          role={element.role}
          text={element.text}
          time={element.time}
          location={element.location}
        />
      );
    });
  };

  return (
    <div className="page text-custom-3 text-base" contentEditable="false">
      <div className="grid grid-cols-6 gap-4">
        <div className="col-span-6 mb-4">
          <div className="text-2xl leading-8 font-bold text-custom-3">
            Ben Krock√©
          </div>
          <div className="text-xl font-medium text-custom-4">
            Software Engineer
          </div>
        </div>
        <div className="col-span-4">
          <Section className="col-span-4" title="Work Experience">
            {getElements(experience)}
          </Section>
          <Section className="col-span-4" title="Projects">
            {getElements(projects)}
          </Section>
        </div>
        <div className="col-span-2">
          <Section className="col-span-2" title="Contact">
            <a href="tel:+31610195369">
              <IconRow icon={<FontAwesomeIcon icon={faPhone} />}>
                +31 6 101 953 69
              </IconRow>
            </a>
            <a href="mailto:benkrocke@gmail.com">
              <IconRow icon={<FontAwesomeIcon icon={faEnvelope} />}>
                benkrocke@gmail.com
              </IconRow>
            </a>
            <a target="_blank" href="https://www.linkedin.com/in/benkrocke">
              <IconRow
                icon={
                  <FontAwesomeIcon
                    style={{ width: "12px" }}
                    icon={faLinkedin}
                  />
                }
              >
                linkedin.com/in/benkrocke
              </IconRow>
            </a>
            <a target="_blank" href="https://www.github.com/benkrocke">
              <IconRow icon={<FontAwesomeIcon icon={faGithub} />}>
                github.com/benkrocke
              </IconRow>
            </a>
          </Section>
          <Section className="col-span-2" title="Technologies">
            {getBadges(technologies)}
          </Section>
          <Section className="col-span-2" title="Skills">
            {getBadges(skills)}
          </Section>
          <Section className="col-span-2" title="Education">
            {getElements(education)}
          </Section>
        </div>
      </div>
    </div>
  );
}

export default App;
